    p{
      font-size: 13px;
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

}



/* Product page */

.product-list-box {
  position: relative;

  img {
    max-width: 220px;
    height: auto;
    margin: 0 auto;
    display: block;
  }
  .product-action {
    position: absolute;
    right: 10px;
    top: 10px;
    display: none;
  }
  .detail {
    background: @lightdark;
    padding: 20px;
    margin: 0px -10px -10px;

    h4{
      a{
        white-space: nowrap;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 16px;
      font-weight: 600;
      }
    }

    .rating {
      ul {
        margin-left: 0px;
        li{
          padding: 0px;
          a {
            color: @warning;
            font-size: 15px;
            margin-bottom: 0;
            padding-right: 4px;
          }
        }
      }
    }
  }

  &:hover {
    .product-action {
      display: block;
    }
  }

}




/* =============
   Accounts pages
============= */


.account-pages {
  background: url("../images/agsquare.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.wrapper-page {
  margin: 5% auto;
  position: relative;
  width: 420px;

  .card-box{
    border: 1px solid rgba(54, 64, 74, 0.1);
  }
}

.panel-pages {
  border-radius: 6px;

  .panel-body {
    padding: 30px;
  }
  .panel-heading {
    -moz-border-radius: 6px 6px 0px 0px;
    -webkit-border-radius: 6px 6px 0px 0px;
    border-radius: 6px 6px 0px 0px;
    padding: 40px 20px;
    position: relative;
    h3 {
      position: relative;
      z-index: 999;
    }
  }
}

.user-thumb {
  position: relative;
  z-index: 999;
  img {
    height: 88px;
    margin: 0px auto;
    width: 88px;
  }
}

.ex-page-content {
  .text-error {
    color: #252932;
    font-size: 98px;
    font-weight: 700;
    line-height: 150px;

    i {
      font-size: 78px;
      padding: 0px 10px;
    }
  }
}

/* signup-signin-page */

.signup-signin-page {
  width: auto;
}



/* =============
   Tree view page
============= */


.jstree-default {
  .jstree-clicked {
    background: fade(@custom, 40%);
    box-shadow: none;
  }
  .jstree-hovered {
    background: fade(@custom, 20%);
    box-shadow: none;
  }
  .jstree-wholerow-clicked {
    background: fade(@custom, 40%);
  }
  .jstree-wholerow-hovered {
    background: fade(@custom, 20%);
  }
  .zmdi {
    font-size: 16px;
  }
}


/* =============
   Tour page
============= */

div.hopscotch-bubble {
  border: 3px solid @primary;
  .border-radius(5px);

  .hopscotch-next {
    background-color: @primary !important;
    background-image: none !important;
    border-color: @primary !important;
    text-shadow: none !important;
    margin: 0 0 0 5px !important;
  }
  .hopscotch-prev {
    background-color: @primary !important;
    background-image: none !important;
    border-color: @primary !important;
    text-shadow: none !important;
    color: @white !important;
  }
}
div.hopscotch-bubble .hopscotch-bubble-number {
  background: @custom;
  padding: 0px;
  .border-radius(50%);
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.left .hopscotch-bubble-arrow-border {
    border-right: 19px solid @primary;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.right .hopscotch-bubble-arrow {
    border-left: 19px solid @primary;
    left: -2px;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.left .hopscotch-bubble-arrow {
    border: none;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.right .hopscotch-bubble-arrow-border {
  border-left: 0px solid @primary;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.up .hopscotch-bubble-arrow {
  border-bottom: 19px solid @primary;
  top: 0px;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.up .hopscotch-bubble-arrow-border {
  border-bottom: 0px solid rgba(0,0,0,.5);
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.down .hopscotch-bubble-arrow {
  border-top: 19px solid @primary;
  top: -2px;
}
div.hopscotch-bubble .hopscotch-bubble-arrow-container.down .hopscotch-bubble-arrow-border {
  border-top: 0px solid rgba(0,0,0,.5);
}

